import { createSlice } from '@reduxjs/toolkit'

//INTERNAL ACTIONS
const slice = createSlice({
  name: 'options',
  initialState: {},
  reducers: {
    setLoOptions: (state, action) => {
      const options = action.payload
      return {
        ...state,
        ...options
      }
    }
  }
})

//EXPORTS
export default slice.reducer

export const actions = {
  ...slice.actions
}
