import { createAction } from '@reduxjs/toolkit'

import { 
  put,
  takeEvery
} from 'redux-saga/effects'

import actionsConstants from '../../activities/action'

import { actionSuccess, actionError } from '../../../utils/actions/actionOut'

import { process } from './processor'

const setUserData = createAction(actionsConstants.SET_USERDATA)
const clearUserData = createAction(actionsConstants.CLEAR_USERDATA)

function* watchSetUserdataExternal() {
  yield takeEvery(setUserData.type, setUserDataExternal)
}

function* watchClearUserdataExternal() {
  yield takeEvery(clearUserData.type, clearUserDataExternal)
}

function* setUserDataExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('setUserData', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })
  } catch (e) {
    yield actionError(action, e)
  }
}

function* clearUserDataExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('clearUserData', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })
  } catch (e) {
    yield actionError(action, e)
  }
}

export const actions = {
  setUserData,
  clearUserData
}

export default {
  watchSetUserdataExternal,
  watchClearUserdataExternal
}

