import { createProcessorManager } from '../../../store/methodStore/internal'

import { actions } from '../reducer'
import { normalizeUserData } from '../schema'

const processorManager = createProcessorManager({
  path: 'sagas',
  name: 'userData',
  processors: {
    setUserData: ({ data, activityType }) => {
      const ret = []
      if (data && data.length) {
        ret.push(actions.setUserData({
          activityType,
          data: normalizeUserData(data).entities.userData
        }))
      }
      return ret
    },
    clearUserData: ({ ids, activityType }) => {
      const ret = []
      if (ids && ids.length) {
        ret.push(actions.clearUserData({
          activityType,
          data: ids
        }))
      }
      return ret
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors
