import { schema, normalize } from 'normalizr'

const validationDataEntry = new schema.Entity('validationData', {})

const validationData = [validationDataEntry]

export default validationData

export const normalizeValidationDataEntry = (data) => {
  return normalize(data, validationDataEntry)
}

export const normalizeValidationData = (data) => {
  return normalize(data, validationData)
}
