import { createSlice } from '@reduxjs/toolkit'
import { process } from './processor'

const slice = createSlice({
  name: 'activities',
  initialState: {},
  reducers: {
    addActivities: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'addActivities', 
        activityType, 
        [state, action.payload]
      )
      
      if (processedState) {
        return processedState
      }
    },
    clearActivities: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'clearActivities', 
        activityType, 
        [state, action.payload]
      )
      
      if (processedState) {
        return processedState
      }
    },
    beginAction: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'beginAction', 
        activityType, 
        [state, action.payload]
      )
      if (processedState) {
        return processedState
      }
     
    },
    endAction: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'endAction', 
        activityType, 
        [state, action.payload]
      )
      if (processedState) {
        return processedState
      }
    },
    setVisible: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'setVisible', 
        activityType, 
        [state, action.payload]
      )
      if (processedState) {
        return processedState
      }
    }
  }
})

//EXPORTS
export default slice.reducer

export const actions = {
  ...slice.actions
}
