import { createSlice } from '@reduxjs/toolkit'

const slice = createSlice({
  name: 'optionList',
  initialState: {},
  reducers: {
    setOptionsList(state, action) {
      const { optionsList } = action.payload
      state = optionsList
      return state
    }
  }
})

export default slice.reducer

export const actions = {
  ...slice.actions
}
