import { createSlice } from '@reduxjs/toolkit'
import { process } from './processor'

const slice = createSlice({
  name: 'itemData',
  initialState: {},
  reducers: {
    addItemData: (state, action) => {   
      const { activityType } = action.payload   
      const processedState = process('addItemData', activityType, [state, action.payload])

      if (processedState) {
        return processedState
      }
    },
    clearItemData: (state, action) => {   
      const { activityType } = action.payload   
      const processedState = process('clearItemData', activityType, [state, action.payload])

      if (processedState) {
        return processedState
      }
    }
  }
})

//EXPORTS
export default slice.reducer

export const actions = {
  ...slice.actions
}
