import createCachedSelector from 're-reselect'
import { createDraftSafeSelector as createSelector } from '@reduxjs/toolkit'

const getState = (state) => state.activities
const getActivityId = (state, props) => props.activityId 

export const getActivitiesAsList = createSelector(
  [getState],
  (activities) => {
    return activities.valueSeq()
  }
)

export const getActivityByActivityId = createCachedSelector(
  [getState, getActivityId],
  (activities, id) => {
    if (!activities || !id) {
      return
    }
    return activities[id]
  }
)(getActivityId)

export const getActivityScreenIdByActivityId = createCachedSelector(
  [getState, getActivityId],
  (activities, id) => {
    if (!activities || !id) {
      return
    }
    return activities[id].screenId
  }
)(getActivityId)

export const getActivityTransactionByActivityId = createCachedSelector(
  [getState, getActivityId],
  (activities, id) => {
    if (!activities || !id) {
      return
    }
    return activities[id].transactions
  }
)(getActivityId)
  
