import { createProcessorManager } from '../../../store/methodStore/internal'

import { getSolutionDataListByIds } from '../../solutionData/selector/general'
import { getUserDataListByIds } from '../../userData/selector/general'
import { getItemDataListByIds } from '../../itemData/selector/general'

import { normalizeValidationData } from '../schema'
import { actions } from '../reducer'
import { select } from 'redux-saga/effects'

const processorManager = createProcessorManager({
  path: 'sagas',
  name: 'validationData',
  processors: {
    validate: function*(payload) {
      const { activityType, activityId } = payload

      const solutionData = yield select(getSolutionDataListByIds, payload)
      const itemData = yield select(getItemDataListByIds, payload)
      const userData = yield select(getUserDataListByIds, payload)
    
      const validationData = itemData
        .map(item => {
          let retValue
          let itemUserData = userData.find(el => el.id === item.id)
          let itemSolData = solutionData.find(el => el.id === item.id)
    
          let correct = false
          if (!itemSolData && !itemUserData) {
            correct = true
          } else if (itemSolData && itemUserData && itemUserData.value === itemUserData.value) {
            correct = true
          }
          if (!item.isDistractor) {
            retValue = {
              id: item.id,
              activityId,
              correct,
              filled: itemUserData !== undefined && itemUserData !== null
            }
            return retValue
          }
        })
        .filter(Boolean)
    
      return [actions.addValidation({
        activityType,
        data: normalizeValidationData(validationData).entities.validationData
      })]
    },
    clearValidation: ({ ids, activityType }) => {
      const ret = []
      if (ids && ids.length) {
        ret.push(actions.clearValidation({
          activityType,
          data: ids
        }))
      }
      return ret
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors
