import Actions from '../../actions/interface'

import { dispatch } from '../../store'

import { normalizeObjectPath, getNestedMethodByPath } from '../pathUtils'
import { successSufix, errorSufix } from './actionOut'
import { addListener, removeListener } from './eventManager'

const actionIn = (action, data) => {
  const methodPath = normalizeObjectPath(action, { all: 'lower', last: 'camel' })
  const method = getNestedMethodByPath(Actions, methodPath)
  
  const successAction = `${action}${successSufix}`
  const errorAction = `${action}${errorSufix}`
 
  const promise = new Promise((resolve, reject) => {
    const onEvent = ({ event, data }) => {
      removeListener(successAction, onEvent)
      removeListener(errorAction, onEvent)
      if (event === successAction) {
        resolve(data || true)
      } else {
        reject(event)
      }
    }
    if (method) {
      addListener(successAction, onEvent)
      addListener(errorAction, onEvent)
      
      dispatch(method.call(null, data))
    } else {
      reject(`no action found for ${action}`)
    }
  })
  return promise
}

export default actionIn
