import { actions as activityActions } from '../reducer'
import { actions as itemDataActions } from '../../itemData/reducer'
import { actions as solutionDataActions } from '../../solutionData/reducer'
import { actions as uiDataActions } from '../../uiData/reducer'
import { actions as userDataActions } from '../../userData/reducer'
import { actions as validationDataActions } from '../../validationData/reducer'

import { normalizeActivitiesList } from '../schema/index'
import { getActivityByActivityId } from '../selector'
import { createProcessorManager } from '../../../store/methodStore/internal'
import { select } from 'redux-saga/effects'

const processorManager = createProcessorManager({
  path: 'sagas',
  name: 'activities',
  processors: {
    addActivities: (payload) => {
      const { id, items, solutions, activityType, visible = true } = payload

      const addId = (list, activityId) => {
        if (!list) {
          return
        }
        return list.map((el, key) => ({
          ...el, 
          id: `${activityId}_${el.index || key}`,
          activityId
        }))
      }

      const mappedItemData = addId(items, id)
      const mappedSolutionData = addId(solutions, id)

      const baseActivity = {
        ...payload,
        itemData: mappedItemData,
        solutionData: mappedSolutionData,
        validationType: Boolean(mappedSolutionData && mappedSolutionData.length),
        transactions:[],
        visible
      }

      delete baseActivity['items']
      delete baseActivity['solutions']

      const normalized = normalizeActivitiesList([baseActivity])
      const { activities, itemData, solutionData } = normalized.entities
      
      const returnValue = []
      if (activities) {
        returnValue.push(activityActions.addActivities({ activityType, data: activities }))
      }
      if (itemData) {
        returnValue.push(itemDataActions.addItemData({ activityType, data: itemData }))
      }
      if (solutionData) {
        returnValue.push(solutionDataActions.addSolutionData({ activityType, data: solutionData }))
      }
      return returnValue
    },
    removeActivities: function* ({ id, activityType }) {
      const activity = yield select(getActivityByActivityId, {
        activityId: id,
        activityType
      })
      
      const ret = [
        activityActions.clearActivities({
          activityType,
          data: [activity.id]
        }),
        itemDataActions.clearItemData({
          activityType,
          data: activity.itemData
        }),
        solutionDataActions.clearSolutionData({
          activityType,
          data: activity.itemData
        }),
        uiDataActions.clearUIData({
          activityType,
          data: activity.itemData
        }),
        userDataActions.clearUserData({
          activityType,
          data: activity.itemData
        }),
        validationDataActions.clearValidation({
          activityType,
          data: activity.itemData
        })
      ]

      return ret
    },
    beginAction: ({ id, activityType, transaction }) => {
      const returnValue = [
        activityActions.beginAction({
          activityType,
          data: [{
            id,
            transaction
          }]
        })
      ]
      return returnValue
    },
    endAction: ({ id, activityType, transaction }) => {
      const returnValue = [
        activityActions.endAction({
          activityType,
          data: [{
            id,
            transaction
          }]
        })
      ]
      return returnValue
    },
    setVisible: ({ id, activityType, visible }) => {
      const returnValue = [
        activityActions.setVisible({
          activityType,
          data: [{
            id,
            visible
          }]
        })
      ]
      return returnValue
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors
