import { organizeReturn } from '../../activities/utils/return'
import { select } from '../selector'
import sortBy from 'lodash/sortBy'

export const getFilled = (state, params) => {
  const { activityType } = params
  const items = select(
    'getActivityUserDataFilled',
    activityType,
    [state, params]
  )
  return organizeReturn(items)
}

export const getUserData = (state, params) => {
  const { activityType } = params
  const items = select(
    'getActivityUserData',
    activityType,
    [state, params]
  )
  return sortBy(items, 'id')
}
