import { organizeReturn } from '../../activities/utils/return'

import { select } from '../selector'
import { filterElements } from '../selector/filter'

export const getAllItems = (state, props) => {
  const { activityType } = props

  const items = select(
    'getActivityItemData',
    activityType,
    [state, props]
  )
  return organizeReturn(items)
}

export const getMaximumScore = async(state, props) => {
  const { activityType } = props

  const items = select(
    'getActivityItemDataScorable',
    activityType,
    [state, props]
  )
  return items?.length
}

export const getItemsToValidate = async(state, props) => {
  const { activityType } = props

  let items = select(
    'getActivityItemDataToValidate',
    activityType,
    [state, props]
  )

  items = filterElements(items, state, props)

  return organizeReturn(items) 
}

export const getScorableItems = async(state, props) => {
  const { activityType } = props
  
  let items = select(
    'getActivityItemDataScorable',
    activityType,
    [state, props]
  )
  items = filterElements(items, state, props)
  return organizeReturn(items) 
}

export const hasScorableItems = async(state, props) => {
  let items = await getScorableItems(state, props)
  return items.length > 0
}
