import { combineReducers } from 'redux'

import options from '../modules/options/features'
import lo from '../modules/lo/features'
import screens from '../modules/screens/features'
import activities from '../modules/activities/reducer'
import itemData from '../modules/itemData/reducer'
import solutionData from '../modules/solutionData/reducer'
import uiData from '../modules/uiData/reducer'
import userData from '../modules/userData/reducer'
import validationData from '../modules/validationData/reducer'

let reducer = {
  options,
  lo,
  screens,
  activities,
  itemData,
  solutionData,
  uiData,
  userData,
  validationData
}

export const getRootReducer = (newReducer = {}) => {
  Object.entries(newReducer).forEach(([key, value]) => {
    if (reducer[key]) {
      if (reducer[key] !== value) {
        throw Error(`overwriting reducer ${key}`)
      }
    } else {
      reducer[key] = value
    }
  })
  
  return combineReducers({
    ...reducer
  })
}
