import { createProcessorManager } from '../../../store/methodStore/internal'

const processorManager = createProcessorManager({
  path: 'reducers',
  name: 'activities',
  processors: {
    addActivities: (state, { data }) => {
      return {
        ...state,
        ...data
      }
    },
    clearActivities: (state, { data }) => {
      return Object.keys(state).reduce((acc, id) => {
        if (!data.includes(id)) {
          acc[id] = state[id]
        }
        return acc
      }, {})
    },
    beginAction: (state, { data }) => {
      try {
        data.forEach(({ id, transaction }) => {
          if (state[id]) {
            state[id].transactions = state[id].transactions || []
            state[id].transactions.push(transaction)
          }
        })
      } catch (e) {
        throw new Error(e.message)
      }
    },
    endAction: (state, { data }) => {
      try { 
        data.forEach(({ id, transaction }) => {
          if (state[id] && state[id].transactions) {
            const transactionIndex = state[id].transactions.findIndex(tr => tr === transaction)
            if (transactionIndex >= 0) {
              state[id].transactions.splice(transactionIndex, 1)
            } 
          }
        })
      } catch (e) {
        throw new Error (e.message)
      }
    },
    setVisible: (state, { data }) => {
      try {
        data.forEach(({ id, visible }) => {
          if (state[id]) {
            state[id].visible = visible
          }
        })
      } catch (e) {
        throw new Error(e.message)
      }
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process 
export const processors = processorManager.processors 
