import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../selector/utils'
import { mapFilterToAcceptedValues } from './utils'

import { getActivityValidationData } from '../../validationData/selector/general'
import { getActivityUserData } from '../../userData/selector/general'
import { getActivityUIData } from '../../uiData/selector/general'

const getState = (state) => state.itemData
const getActivityId = (state, props) => props.activityId
const getIds = (state, props) => props.ids
const getIdsToJson = (state, props) => JSON.stringify(props.ids.sort((a, b) => a - b))
const getFilterValue = (state, props) => props?.filter?.value
const getFilterType = (state, props) => props?.filter?.type
const getFilterName = (state, props) => props?.filter?.name

const getItemDataAsList = createArraySelector(
  [getState],
  (items) => {
    if (!items) {
      return
    }
    return Object.values(items)
  }
)

export const getItemDataListByIds = createCachedSelector(
  [getState, getIds],
  (items, ids) => {
    if (!items) {
      return
    }
    let idsToFetch = ids || []
    
    return idsToFetch.map(id => items[id]).filter(Boolean)
  }
)(getIdsToJson, { selectorCreator: createArraySelector })

export const getActivityItemData = createCachedSelector(
  [getItemDataAsList, getActivityId],
  (items, activityId) => {
    if (!items || !activityId) {
      return
    }
    return items.filter(el => el.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataScorable = createCachedSelector(
  [getActivityItemData],
  (items) => {
    if (!items) {
      return
    }

    return items.filter(item => !item.isDistractor)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataToValidate = createCachedSelector(
  [getActivityItemData, getActivityValidationData, getActivityUserData],
  (items, validationData, userData) => {
    if (!items || !validationData || !userData) {
      return
    }
    return items.filter(item => {
      const itemUserData = userData.find(({ id }) => id === item.id)
      const itemValidationData = validationData.find(el => el.id === item.id)
      return !itemValidationData || (itemUserData && itemValidationData.time < itemUserData.time)
    })
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataFilteredById = createCachedSelector(
  [getActivityItemData, getFilterValue, getFilterType],
  (items, filterValue, filterType) => {
    if (!items) {
      return
    }
    let elements = items
    if (filterValue) {
      const allowedIds = mapFilterToAcceptedValues(
        {
          type: filterType, 
          value: filterValue
        }, 
        elements.map(el => el.id)
      )
      elements = elements.filter(el => allowedIds.includes(el.id))
    }
    return elements
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataFilteredByValidationData = createCachedSelector(
  [getActivityItemData, getActivityValidationData, getFilterValue, getFilterType],
  (items, validationData, filterValue, filterType) => {
    if (!items || !validationData) {
      return
    }
    let elements = items
    if (filterValue) {
      const filterTo = mapFilterToAcceptedValues(
        {
          type: filterType, 
          value: filterValue
        },
        ['correct', 'incorrect', null]
      )
      elements = items.filter(item => {
        let itemValData = validationData.find((el) => el.id === item.id)
        return (!itemValData && filterTo.includes(null)) ||
          (itemValData && itemValData.correct && filterTo.includes('correct')) ||
          (itemValData && !itemValData.correct && filterTo.includes('incorrect'))
      })
    }
    return elements
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataFilteredByUiData = createCachedSelector(
  [getActivityItemData, getActivityUIData, getFilterValue, getFilterType, getFilterName],
  (items, uiData, filterValue, filterType, filterName) => {
    if (!items || !uiData) {
      return
    }
    let elements = items
    if (filterValue && filterName) {
      const validValues = {
        'validationType': ['correct_incorrect', 'correct_incorrect_empty', null],
        'dataType': ['user', 'solution', null],
        'enabled': [true, false]
      }
      const filterTo = mapFilterToAcceptedValues(
        {
          type: filterType, 
          value: filterValue
        },
        validValues[filterName]
      )

      elements = items.filter(item => {
        let itemUiData = uiData.find(el => el.id === item.id)
        return itemUiData && filterTo.includes(itemUiData[filterName])
      })
    }
    return elements
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityItemDataFilteredByFilled = createCachedSelector(
  [getActivityItemData, getActivityUserData, getFilterType, getFilterValue],
  (items, userData, filterType, filterValue) => {
    if (!items || !userData) {
      return
    }
    let elements = items
    if (filterType && filterValue) {
      const filterTo = mapFilterToAcceptedValues(
        {
          type: filterType, 
          value: filterValue
        },
        [true, false]
      )
      elements = items.filter(item => {
        let itemUserData = userData.find(el => el.id === item.id)
        return ((!itemUserData || !item.userData.value) && filterTo.includes(false)) ||
          (itemUserData && itemUserData.value && filterTo.includes(true))
      })
    }
    return elements
  }
)(getActivityId, { selectorCreator: createArraySelector })

