import { createSlice } from '@reduxjs/toolkit'
import { actions as activitiesAction } from '../../activities/reducer'

//INTERNAL ACTIONS
const slice = createSlice({
  name: 'options',
  initialState: {},
  reducers: {
    addScreens: (state, action) => {
      const screens = action.payload
      return {
        ...state,
        ...screens
      }
    }
  },
  extraReducers: {
    [activitiesAction.addActivities.type]: (state, action) => {
      const { data } = action.payload
      Object.values(data).forEach(({ screenId, id }) => {
        state[screenId].activities.push(id)
      })
    }
  }
})

//EXPORTS
export default slice.reducer

export const actions = {
  ...slice.actions
}
