import { createAction } from '@reduxjs/toolkit'

import { 
  put,
  takeEvery
} from 'redux-saga/effects'

import actionsConstants from '../action'

import { actionSuccess, actionError } from '../../../utils/actions/actionOut'

import { process } from './processor'

const addActivity = createAction(actionsConstants.ADD_ACTIVITY)

const removeActivity = createAction(actionsConstants.REMOVE_ACTIVITY)

const beginAction = createAction(actionsConstants.BEGIN_ACTION)

const endAction = createAction(actionsConstants.END_ACTION)

const setVisible = createAction(actionsConstants.SET_VISIBLE)

function* watchAddActivityExternal() {
  yield takeEvery(addActivity.type, addActivityExternal)
}

function* watchRemoveActivityExternal() {
  yield takeEvery(removeActivity.type, removeActivityExternal)
}

function* watchBeginActionExternal() {
  yield takeEvery(beginAction.type, beginActionExternal)
}

function* watchEndActionExternal() {
  yield takeEvery(endAction.type, endActionExternal)
}

function* watchSetVisibleExternal() {
  yield takeEvery(setVisible.type, setVisibleExternal)
}

function* addActivityExternal(action) {
  try {
    const { id, activityType } = action.payload
    const actions = yield process('addActivities', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })
   
    yield (actionSuccess(action, { id }))
  } catch (e) {
    yield (actionError(action, e))
    throw Error(e)
  }
}

function* removeActivityExternal(action) {
  try {
    const { id, activityType } = action.payload
    const actions = yield process('removeActivities', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })
   
    yield (actionSuccess(action, { id }))
  } catch (e) {
    yield (actionError(action, e))
    throw new Error(e.message)
  }
}

function* beginActionExternal(action) {
  try {
    const { id, activityType } = action.payload
    const actions = yield process('beginAction', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })
   
    yield (actionSuccess(action, { id }))
  } catch (e) {
    yield (actionError(action, e))
    throw new Error(e.message)
  }
}

function* endActionExternal(action) {
  try {
    const { id, activityType } = action.payload
    const actions = yield process('endAction', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })
   
    yield (actionSuccess(action, { id }))
  } catch (e) {
    yield (actionError(action, e))
    throw new Error(e.message)
  }
}

function* setVisibleExternal(action) {
  try {
    const { id, activityType } = action.payload
    const actions = yield process('setVisible', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })
   
    yield (actionSuccess(action, { id }))
  } catch (e) {
    yield (actionError(action, e))
    throw new Error(e.message)
  }
}

export const actions = {
  addActivity,
  removeActivity,
  beginAction,
  endAction,
  setVisible
}

export default {
  watchAddActivityExternal,
  watchRemoveActivityExternal,
  watchBeginActionExternal,
  watchEndActionExternal,
  watchSetVisibleExternal
}
