import { organizeReturn } from '../../activities/utils/return'

import { select } from '../selector'

const getItemsByFilter = (state, props) => {
  const { activityType } = props
  
  const items = select(
    'getActivityUIDataByFilter',
    activityType,
    [state, props]
  )
  return organizeReturn(items)
}

export const getItemsByEnabled = (state, props) => {
  return getItemsByFilter(state, {
    ...props,
    filters: {
      enabled: props.enabled
    }
  })
}

export const getItemsByDataType = (state, props) => {
  return getItemsByFilter(state, {
    ...props,
    filters: {
      dataType: props.dataType
    }
  })
}

export const getItemsByValidationType = (state, props) => {
  return getItemsByFilter(state, {
    ...props,
    filters: {
      validationType: props.validationType
    }
  })
}
