import { schema, normalize } from 'normalizr'

const solutionDataEntry = new schema.Entity('solutionData', {})

const solutionData = [solutionDataEntry]

export default solutionData

export const normalizeSolutionDataEntry = (data) => {
  return normalize(data, solutionDataEntry)
}

export const normalizeSolutionData = (data) => {
  return normalize(data, solutionData)
}
