import { getNestedMethodByPath } from '../../utils/pathUtils'
import merge from 'lodash/merge'

export const register = (hash, path, method) => {
  const steps = path.split('.')

  const ref = {}
  steps.reduce((acc, val, k) => {
    if (k < steps.length - 1) {
      acc[val] = acc[val] || {}
    } else {
      acc[val] = method
    }
    return acc[val]
  }, ref)

  if (getNestedMethodByPath(hash, path)) {
    logger.warn(`overwriting processor in path ${path}`)
  }
  return merge(hash, ref)
}

export const process = (hash, path, data, fallback) => {
  let method = getNestedMethodByPath(hash, path)
  let fallbackPath = fallback
  if (!method) {
    if (!fallback) {
      let steps = path.split('.')
      steps = steps.splice(steps.length - 1, 1, 'general')
      fallbackPath = steps.join('.')
    }
    method = getNestedMethodByPath(hash, fallbackPath)
  }
  if (method) {
    return method.apply(null, [...[data].flat()])
  } else {
    throw Error(`no methods ${path} nor ${fallbackPath} found in processors`)
  }
}
