import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../selector/utils'

const getState = (state) => state.validationData
const getIds = (state, props) => props.ids
const getIdsToJson = (state, props) => JSON.stringify(props.ids.sort((a, b) => a - b))
const getActivityId = (state, props) => props.activityId
const getCorrect = (state, props) => props?.correct

const getValidationDataAsList = createArraySelector(
  [getState],
  (items) => {
    if (!items) {
      return
    }
    return Object.values(items)
  }
)

export const getValidationDataListByIds = createCachedSelector(
  [getState, getIds],
  (items, ids) => {
    if (!items) {
      return
    }
    let idsToFetch = ids || []
    
    return idsToFetch.map(id => items[id]).filter(Boolean)
  }
)(getIdsToJson, { selectorCreator: createArraySelector })

export const getActivityValidationData = createCachedSelector(
  [getValidationDataAsList, getActivityId],
  (items, activityId) => {
    if (!items || !activityId) {
      return
    }
    return items.filter(el => el.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityValidationDataByCorrect = createArraySelector(
  [getActivityValidationData, getCorrect],
  (items, correct) => {
    if (!items || correct === null || correct === undefined) {
      return 
    }
    return items.filter(item => item['correct'] === correct)
  }
)

