import { dispatchEvent } from './eventManager'

export const successSufix = '_SUCCESS'
export const errorSufix = '_ERROR'

export const actionSuccess = (action, payload) => {
  dispatchEvent(`${action.type}${successSufix}`, payload)
}

export const actionError = (action, message) => {
  window.logger.error(`ReactError: failed to complete setState for ${action.type}`, message)
  dispatchEvent(`${action.type}${errorSufix}`, message)
}
