import { 
  process as storeProcess, 
  register as storeRegister 
} from './methodsStore'

let hash = {}

export const process = (path, data, fallback) => {
  return storeProcess(hash, path, data, fallback)
}
export const register = (path, method) => {
  hash = storeRegister(hash, path, method)
}

export const createProcessorManager = ({ path, name, processors }) => {
  const suffix = 'general'
  const prefix = `${path}.${name}`
  
  Object.entries(processors).forEach(([methodName, method]) => {
    register(`${prefix}.${methodName}.${suffix}`, method)
  })

  const processOnScope = (methodName, extension, data) => {
    return process(
      `${prefix}.${methodName}.${extension}`,
      data,
      `${prefix}.${methodName}.${suffix}`
    )
  }

  const registerOnScope = (methodName, extension, method) => {
    register(`${prefix}.${methodName}.${extension}`, method)
  }

  return {
    register: registerOnScope,
    process: processOnScope,
    processors
  }
}
