import { createAction } from '@reduxjs/toolkit'

import { 
  put,
  takeEvery
} from 'redux-saga/effects'

import actionsConstants from '../../activities/action'

import { actionSuccess, actionError } from '../../../utils/actions/actionOut'

import { process } from './processor'

const setEnabled = createAction(actionsConstants.SET_ENABLED)

const setDataType = createAction(actionsConstants.SET_DATATYPE)

const setValidationType = createAction(actionsConstants.SET_VALIDATIONTYPE)

const setExtraUI = createAction(actionsConstants.SET_EXTRAUI)

const setBatchUI = createAction(actionsConstants.SET_BATCHUI)

function* watchSetEnabledExternal() {
  yield takeEvery(setEnabled.type, setEnabledExternal)
}

function* watchSetDataTypeExternal() {
  yield takeEvery(setDataType.type, setDataTypeExternal)
}

function* watchSetValidationTypeExternal() {
  yield takeEvery(setValidationType.type, setValidationTypeExternal)
}

function* watchSetExtraUiExternal() {
  yield takeEvery(setExtraUI.type, setExtraUIExternal)
}

function* watchSetBatchUiExternal() {
  yield takeEvery(setBatchUI.type, setBatchUIExternal)
}

function* setEnabledExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('setEnabled', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })

  } catch (e) {
    yield actionError(action, e)
  }
}

function* setDataTypeExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('setDataType', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })

  } catch (e) {
    yield actionError(action, e)
  }
}

function* setValidationTypeExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('setValidationType', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })

  } catch (e) {
    yield actionError(action, e)
  }
}

function* setExtraUIExternal(action) {
  try {
    const { activityId, activityType } = action.payload
    const actions = yield process('setExtraUI', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { id: activityId })

  } catch (e) {
    yield actionError(action, e)
  }
}

function* setBatchUIExternal(action) {
  try {
    const { activityId, activityType } = action.payload
    const actions = yield process('setBatchUI', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { id: activityId })

  } catch (e) {
    yield actionError(action, e)
  }
}

export const actions = {
  setEnabled,
  setDataType,
  setValidationType,
  setExtraUI,
  setBatchUI
}

export default {
  watchSetEnabledExternal,
  watchSetDataTypeExternal,
  watchSetValidationTypeExternal,
  watchSetExtraUiExternal,
  watchSetBatchUiExternal
}

