import { all } from 'redux-saga/effects'

import { sagas as loSagas } from './modules/lo/features'
import { sagas as screensSagas } from './modules/screens/features'
import activitiesSagas from './modules/activities/saga'
import uiDataSagas from './modules/uiData/saga'
import userDataSagas from './modules/userData/saga'
import validationDataSaga from './modules/validationData/saga'

export default function* rootSaga() {
  const watchers = Object.values({
    ...loSagas,
    ...screensSagas,
    ...activitiesSagas,
    ...uiDataSagas,
    ...userDataSagas,
    ...validationDataSaga
  }).map(watcher => watcher.apply())
  yield all(watchers)
}

export const root = {
  ...loSagas,
  ...screensSagas,
  ...activitiesSagas,
  ...uiDataSagas,
  ...userDataSagas,
  ...validationDataSaga
}

const extendedSagas = {}

export const registerSagas = (sagas = root, middleware) => {
  const newSagas = []
  Object.entries(sagas).forEach(([key, value]) => {
    if (extendedSagas[key]) {
      if (extendedSagas[key] !== value) {
        throw Error (`overwriting saga ${key}`)
      }
    } else {
      extendedSagas[key] = value
      newSagas.push(value)
    }
  })

  function* newSagasWatchers() {
    yield all(newSagas.map(saga => saga.apply()))
  }

  middleware.run(newSagasWatchers)
}

