import { schema, normalize } from 'normalizr'

import screen from '../../screens/schema'

import validationData from '../../validationData/schema'
import uiData from '../../uiData/schema'
import solutionData from '../../solutionData/schema'
import userData from '../../userData/schema'
import itemData from '../../itemData/schema'

const activity = new schema.Entity('activities', {
  screen,
  validationData,
  uiData,
  solutionData,
  userData,
  itemData
})

const activities = [activity]

export const normalizeActivity = (data) => {
  return normalize(data, activity)
}

export const normalizeActivitiesList = (data) => {
  return normalize(data, activities)
}

