import { schema, normalize } from 'normalizr'

const uiDataEntry = new schema.Entity('uiData', {})

const uiData = [uiDataEntry]

export default uiData

export const normalizeUiDataEntry = (data) => {
  return normalize(data, uiDataEntry)
}

export const normalizeUiData = (data) => {
  return normalize(data, uiData)
}
