/*eslint-env node, browser */

import { configureStore } from '@reduxjs/toolkit'
import createSagaMiddleware from 'redux-saga'

import { getRootReducer } from '../reducers'
import { registerSagas } from '../sagas'

const sagaMiddleware = createSagaMiddleware()

if (process.env.NODE_ENV === 'development' && module.hot) {
  module.hot.accept('./reducer', () => {
    const newRootReducer = require('./reducer').default
    store.replaceReducer(newRootReducer)
  })
}

export const injectReducer = (newReducer) => {
  const rootReducer = getRootReducer(newReducer)
  store.replaceReducer(rootReducer)
}

export const injectSagas = (newSaga) => {
  registerSagas(newSaga, sagaMiddleware)
}

const store = configureStore({ 
  reducer: getRootReducer(),
  middleware: [sagaMiddleware]
})
injectSagas()

export default store

export const getState = store.getState
export const dispatch = store.dispatch
