import { createProcessorManager } from '../../../store/methodStore/internal'

import { actions } from '../reducer'

export const createAction = (payload, key) => {
  const { ids, activityType } = payload
  return actions.setUIData({ 
    activityType,
    data: ids.map((id) => ({ 
      id, 
      [key]: payload[key]
    }))
  })
}

const processorManager = createProcessorManager({
  path: 'sagas',
  name: 'uiData',
  processors: {
    setEnabled: (payload) => [createAction(payload, 'enabled')],
    setDataType: (payload) => [createAction(payload, 'dataType')],
    setValidationType: (payload) => [createAction(payload, 'validationType')],
    setExtraUI: (payload) => [createAction(payload, 'extra')],
    setBatchUI: (payload) => {
      const { ids, activityType, data } = payload
      const acceptedKeys = ['extraUI', 'enabled', 'validationType', 'dataType']
      const validHash = Object.entries(data).reduce((acc, [key, val]) => {
        if (acceptedKeys.includes(key)) {
          acc[key] = val
        }
        return acc
      }, {})
      return [actions.setUIData({
        activityType,
        data: ids.map(id => ({ id, ...validHash }))
      })]
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors
