import { createSlice } from '@reduxjs/toolkit'
import { actions as itemActions } from '../../itemData/reducer'
import { process } from './processor'

const slice = createSlice({
  name: 'uiData',
  initialState: {},
  reducers: {
    setUIData: (state, action) => {
      const { activityType } = action.payload
      const processedState = process('setUIData', activityType, [state, action.payload])
      
      if (processedState) {
        return processedState
      }
    },
    clearUIData: (state, action) => {
      const { activityType } = action.payload
      const processedState = process('clearUIData', activityType, [state, action.payload])
      
      if (processedState) {
        return processedState
      }
    }
  },
  extraReducers: {
    [itemActions.addItemData.type]: (state, action) => {
      const { activityType } = action.payload
      const processedState = process(
        'addItemData', 
        activityType,
        [state, action.payload]
      )

      if (processedState) {
        return processedState
      }
    }
  }
})

//EXPORTS
export default slice.reducer

export const actions = {
  ...slice.actions
}
