import Selectors from '../selector/interface'
import { getState, dispatch } from '../store'

import { normalizeObjectPath, getNestedMethodByPath } from './pathUtils'

const selectorFetcher = (methodName, data) => {
  const methodPath = normalizeObjectPath(methodName, { all: 'lower', last: 'camel' })
  const method = getNestedMethodByPath(Selectors, methodPath)
  if (method) {
    return method.apply(null, [getState(), data, dispatch])
  } else {
    logger.warn(`couldnt find getter ${methodName}`)
  }
}

export default selectorFetcher
