import { createAction } from '@reduxjs/toolkit'

import { 
  put,
  takeEvery
} from 'redux-saga/effects'

import actionsConstants from '../../activities/action'

import { actionSuccess, actionError } from '../../../utils/actions/actionOut'

import { process } from './processor'

const validate = createAction(actionsConstants.VALIDATE)
const clearValidation = createAction(actionsConstants.CLEAR_VALIDATION)

function* watchValidateExternal() {
  yield takeEvery(validate.type, validateExternal)
}

function* watchClearValidationExternal() {
  yield takeEvery(clearValidation.type, clearValidationExternal)
}

function* validateExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('validate', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })
  } catch (e) {
    yield actionError(action, e)
    throw new Error(e.message)
  }
}

function* clearValidationExternal(action) {
  try {
    const { activityId, activityType } = action.payload

    const actions = yield process('clearValidation', activityType, action.payload)

    yield* actions.map(function*(act) {
      yield put(act)
    })

    yield actionSuccess(action, { activityId })
  } catch (e) {
    yield actionError(action, e)
  }
}

export const actions = {
  validate,
  clearValidation
}

export default {
  watchValidateExternal,
  watchClearValidationExternal
}

