import createCachedSelector from 're-reselect'
import { createDraftSafeSelector as createSelector } from '@reduxjs/toolkit'
import { createArraySelector } from '../../../selector/utils'

export const getState = (state) => state.uiData
const getIds = (state, props) => props.ids
const getIdsToJson = (state, props) => JSON.stringify(props.ids.sort((a, b) => a - b))
const getActivityId = (state, props) => props.activityId
const getFilters = (state, props) => props?.filters 
const getFiltersJSON = (state, props) => JSON.stringify(props?.filters)

export const getItemsState = (state) => state.uiData.get('items')
export const getGlobalState = (state) => state.uiData.get('global')

const getUIDataAsList = createSelector(
  [getState],
  (items) => {
    if (!items) {
      return
    }
    return Object.values(items)
  }
)

export const getUIDataListByIds = createCachedSelector(
  [getState, getIds],
  (items, ids) => {
    if (!items) {
      return
    }
    let idsToFetch = ids || []
    
    return idsToFetch.map(id => items[id]).filter(Boolean)
  }
)(getIdsToJson)

export const getActivityUIData = createCachedSelector(
  [getUIDataAsList, getActivityId],
  (items, activityId) => {
    if (!items || !activityId) {
      return
    }
    return items.filter(el => el.activityId === activityId)
  })(getActivityId, { selectorCreator: createArraySelector })

export const getActivityUIDataByFilter = createCachedSelector(
  [getActivityUIData, getFilters],
  (items, filter) => {
    if (!items || !items.length || !filter) {
      return
    }
    return items.filter(item => {
      const entries = Object.entries(filter)
      return entries.every(([key, value]) => {
        return item[key] === value
      })
    })
  }
)(() => `${getActivityId}${getFiltersJSON}`)

export const getActivityUIDataEnabled = (state, props) => {
  return getActivityUIDataByFilter(state, { ...props, filters: { enabled: true } })
}

export const getActivityUIDataDisabled = (state, props) => {
  return getActivityUIDataByFilter(state, { ...props, filters: { enabled: false } })
}

export const getActivityUIDataDataTypeUser = (state, props) => {
  return getActivityUIDataByFilter(state, { ...props, filters: { dataType: 'user' } })
}

export const getActivityUIDataDataTypeSolution = (state, props) => {
  return getActivityUIDataByFilter(state, { ...props, filters: { dataType: 'solution' } })
}
