import { createProcessorManager } from '../../../store/methodStore/internal'

export const model = {
  dataType: 'user',
  validationType: null,
  enabled: true,
  extra: []
}

const processorManager = createProcessorManager({
  path: 'reducers',
  name: 'uiData',
  processors: {
    setUIData: (state, { data }) => {
      data.forEach(el => {
        state[el.id] = {
          ...state[el.id],
          ...el
        }
      })
    },
    addItemData: (state, { data }) => {
      const els = Object.values(data).reduce((acc, val) => {
        acc[val.id] = {
          id: val.id,
          activityId: val.activityId,
          ...model
        }
        return acc
      }, {})

      return {
        ...state,
        ...els
      }
    },
    clearUIData: (state, { data }) => {
      return Object.keys(state).reduce((acc, id) => {
        if (!data.includes(id)) {
          acc[id] = state[id]
        }
        return acc
      }, {})
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors

