import { createProcessorManager } from '../../../store/methodStore/internal'

const processorManager = createProcessorManager({
  path: 'reducers',
  name: 'solutionData',
  processors: {
    addSolutionData: (state, { data }) => {
      return {
        ...state,
        ...data
      }
    },
    clearSolutionData:(state, { data }) => {
      return Object.keys(state).reduce((acc, id) => {
        if (!data.includes(id)) {
          acc[id] = state[id]
        }
        return acc
      }, {})
    }
  }
})

export const register = processorManager.register
export const process = processorManager.process
export const processors = processorManager.processors
