import createCachedSelector from 're-reselect'
import { createArraySelector } from '../../../selector/utils'

const getState = (state) => state.userData
const getIds = (state, props) => props.ids
const getIdsToJson = (state, props) => JSON.stringify([...props.ids].sort())
const getActivityId = (state, props) => props.activityId

const getUserDataAsList = createArraySelector(
  [getState],
  (items) => {
    if (!items) {
      return
    }
    return Object.values(items)
  }
)

export const getUserDataListByIds = createCachedSelector(
  [getState, getIds],
  (items, ids) => {
    if (!items) {
      return
    }
    let idsToFetch = ids || []
    
    return idsToFetch.map(id => items[id]).filter(Boolean)
  }
)(getIdsToJson, { selectorCreator: createArraySelector })

export const getActivityUserData = createCachedSelector(
  [getUserDataAsList, getActivityId],
  (items, activityId) => {
    if (!items || !activityId) {
      return
    }
    return items.filter(el => el.activityId === activityId)
  }
)(getActivityId, { selectorCreator: createArraySelector })

export const getActivityUserDataFilled = createCachedSelector(
  [getActivityUserData],
  (activityUserData) => {
    if (!activityUserData) {
      return
    }
    return activityUserData.filter(el => el.value !== null && el.value !== undefined)
  }
)(getActivityId, { selectorCreator: createArraySelector })

