import { organizeReturn } from '../../activities/utils/return'

import { select } from '../selector'

export const getCorrect = async(state, props) => {
  const { activityType } = props

  const items = select(
    'getActivityValidationDataByCorrect',
    activityType,
    [ state,
      {
        ...props,
        correct: true
      }]
  )
  return organizeReturn(items)
}

export const getIncorrect = async(state, props) => {
  const { activityType } = props
  
  const items = select(
    'getActivityValidationDataByCorrect',
    activityType,
    [state,
      {
        ...props,
        correct: false
      }]
  )
  return organizeReturn(items)
}
