
const listeners = {}

const defaultCallback = (event, data) => {
  logger.log(`firing event out of react => ${event}: `, data)
}

let callback
let context

export const addListener = (event, callback, context) => {
  listeners[event] = listeners[event] || []
  listeners[event].push({
    callback,
    context
  })
}

export const removeListener = (event, callback) => {
  if (listeners[event]) {
    listeners[event] = listeners[event].filter(el => el.callback !== callback)
    if (!listeners[event].length) {
      delete listeners[event]
    }
  }
}

export const dispatchEvent = (event, data) => {
  if (listeners[event]) {
    listeners[event].forEach(el => el.callback.apply(el.context || null, [{ event, data }]))
  }
  let cbk = callback || defaultCallback
  cbk.apply(context || null, [event, data])
}

export const addEventsCallback = (cbk, ctx) => {
  callback = cbk
  context = ctx
}
