import { 
  put,
  select,
  takeEvery
} from 'redux-saga/effects'

import { createAction } from '@reduxjs/toolkit'

import {
  actionsConstants
} from '../../actions'

import { getOptionsById } from '../../../options/selector'

import { optionsValidate } from '../../../../utils/optionsValidate'
import { actionSuccess, actionError } from '../../../../utils/actions/actionOut'

import { actions as internalActions } from './reducer'

const setLoOptions = createAction(actionsConstants.SET_LO_OPTIONS)

export function* watchSetLoOptionsExternal() {
  yield takeEvery(setLoOptions.type, setLoOptionsExternal)
}

function* setLoOptionsExternal(action) {
  try {
    const ids = Object.keys(action.payload)
    const optionsValues = yield select(getOptionsById, { ids })
    const validatedOptions = optionsValidate(action.payload, optionsValues, 'lo')
    
    yield put(internalActions.setLoOptions(validatedOptions))
    yield actionSuccess(action)
  } catch (e) {
    yield actionError(action, e)
  }
}

export const actions = {
  setLoOptions
}

export const sagas = {
  watchSetLoOptionsExternal
}
