import { createAction } from '@reduxjs/toolkit'

import { normalizeScreensList } from '../schema'

import { 
  put,
  select,
  takeEvery
} from 'redux-saga/effects'

import {
  actionsConstants
} from '../actions'

import { getOptionsById } from '../../options/selector'

import { optionsValidate } from '../../../utils/optionsValidate'
import { actionSuccess, actionError } from '../../../utils/actions/actionOut'

import { actions as screenActions } from './reducer'

//SAGA
const addScreens = createAction(actionsConstants.ADD_SCREENS)

function* watchAddScreensExternal() {
  yield takeEvery(addScreens.type, addScreensExternal)
}

function* addScreensExternal(action) {
  try {
    const mapedOptions = {}

    const mapMethod = function* (el) {
      const ids = Object.keys(el.options)
      const optionsValues = yield select(getOptionsById, { ids })
      yield mapedOptions[el.id] = optionsValues
    }

    yield* action.payload.map(mapMethod)

    const mapped = action.payload.map(entry => {
      const { id } = entry
      return {
        ...entry,
        options: optionsValidate(entry.options, mapedOptions[id], 'screen')
      }
    })
    const normalized = normalizeScreensList(mapped)
    const { screens } = normalized.entities
    yield put(screenActions.addScreens(screens))
    yield actionSuccess(action, { ids: mapped.map(m => m.id) })
  } catch (e) {
    yield actionError(action, e)
  }
}

export const actions = {
  addScreens
}

export const sagas = {
  watchAddScreensExternal
}
