import { createDraftSafeSelector as createSelector } from '@reduxjs/toolkit'
import createCachedSelector from 're-reselect'

import snakeCase from 'lodash/snakeCase'
import optionsList from '../utils/optionsList'

const getState = (state) => state.options
const getOptionId = (state, props) => props.id
const getOptionsIds = (state, props) => props.ids
export const getOptionName = (state, props, optionName) => optionName

export const getOptionList = createSelector(
  getState, 
  (options) => options.optionList
)

export const getOptionListAsList = createSelector(
  getOptionList,
  (optionList) => {
    if (!optionList) {
      return
    }
    return Object.values(optionList)
  }
)

export const getOptionById = createSelector(
  [getOptionList, getOptionId],
  (optionList, id) => {
    if (!id || !optionList) {
      return
    }
    return optionList[id]
  }
)

export const getOptionsById = createSelector(
  [getOptionList, getOptionsIds],
  (optionList, ids) => {
    if (!ids || !optionsList) {
      return
    }
    return ids.reduce((acc, id) => {
      const v = optionList[id]
      if (v) {
        acc[id] = v
      }
      return acc
    }, {})
  }
)

export const getOptionByName = createCachedSelector(
  [getOptionListAsList, getOptionName],
  (optionlist, name) => {
    if (!name || !optionlist) {
      return
    }

    return optionlist.find(el => snakeCase(el.name) === snakeCase(name))
   
  }
)(getOptionName)

