import React, { lazy, Suspense } from 'react'
import PropTypes from 'prop-types'

const ICCrosswrod = lazy(() => import(/* webpackChunkName: "ICCrossword" */'../../activityTypes/ICCrossword/components/ICCrossword.jsx'))

const ActivityComponentMapper = (props) => {
  const { activityType } = props
  let Component

  switch (activityType) {
    case 'iccrossword':
      Component = ICCrosswrod
      break

    default:
      Component = null
  }

  return (
    <div>
      {(Component &&
        <Suspense fallback={<div>Loading...</div>}>
          <Component {...props}/>
        </Suspense>
      )}
    </div>
  )
}
ActivityComponentMapper.propTypes = {
  activityType: PropTypes.string
}

export default ActivityComponentMapper

